.container {
    margin: 0 auto;
}

@media screen and (max-width: 767px) {
    .container {
        padding-left: 20px;
        padding-right: 20px;
        min-width: 320px;   
    }
}

@media screen and (min-width: 768px) {
    .container {
        max-width: 768px;
    }
}

@media screen and (min-width: 1200px) {
    .container {
        max-width: 1200px;
    }
}